import React, { PureComponent } from "react";
import Card from "./elements/Card";
import { connect } from 'react-redux';
import { getDataNextSteps, themePostTracking, showPopUP } from "../../../../../../../../../actions/themeActions/themeActions";
import { IS_SCREEN_SHOT ,  findFromArray, removeTransFromCss} from './../../../utils'
import '@splidejs/splide/css';
import { Splide, SplideSlide, SplideTrack } from '@splidejs/react-splide';

class CircleSlider extends PureComponent {
  constructor(props) {
    super(props)
    this.secondaryRef = React.createRef();
  }
  state = { cardCount: null, widnowWidth: document.querySelector('.taggshopContainer') ? document.querySelector('.taggshopContainer').clientWidth : ''}

  componentDidMount() {
    this.onCardCountFormation(this.props)
    }


  onCardCountFormation = (props) => {
    const { wall } = this.props;
    const { widnowWidth } = this.state;
    const minPostWidth = wall.Personalization.minimumPostWidth;
    const cardCount = parseInt(widnowWidth / minPostWidth);
    this.setState({ cardCount: cardCount })

  }

  onUpdateData = () => { setTimeout(() => this.requestData(), 1000)}

  requestData = () => {
    const { wall, postData, appendData, hasMoreData, loaderData } = this.props;
    const postCount = wall.ThemeRule.numberOfPosts;
    const timeStamp = Math.floor(Date.now() / 1000);
    if (hasMoreData && !loaderData.isShowMoreLoading) this.props.getDataNextSteps(wall.Wall.id, timeStamp, postCount, appendData.networkID, appendData.after, appendData.heightEvent);
  }

  render() {
    const { appendData, postData, completeDataObject, adjustWidth, hasMoreData, wall, webFilters, languageSetting, newPostData , loaderData } = this.props;
    const { cardCount, widnowWidth } = this.state;

    const sliderSettings1 = {
      type: postData.length < parseInt(widnowWidth / wall.Personalization.minimumPostWidth) ? 'slider' : false,
      perPage: cardCount,
      perMove: 1,
      pagination: false,
    }

    return (
      <div className="ts_cs_post_container" id={window.isEditor ? 'isEditorCls ts_theme_container' : 'ts_theme_container'}>

        <Splide hasTrack={false} options={sliderSettings1} ref={this.secondaryRef}>
          <SplideTrack>
            {
              (newPostData && newPostData.length > 0) ? newPostData.map((item, index) => {
                const cardData = completeDataObject[item];
                return <SplideSlide key={`spli${index}`}><Card itemData={cardData} key={`post_Id_${index}`} itemIndex={index} adjustWidth={adjustWidth} {...this.props} /></SplideSlide>
              }) : null
            }
          </SplideTrack>

          <div className="splide__arrows">
            <div className="splide__arrow--next tb_cs_slider_next_arrow-next" onClick={this.onUpdateData}>
              {(hasMoreData && loaderData.isShowMoreLoading) ?
                <svg width='26px' height='26px' xmlns="http://www.w3.org/2000/svg" viewBox="0 0 100 100" preserveAspectRatio="xMidYMid" className="uil-ring-alt"><rect x="0" y="0" width="100" height="100" fill="none" className="bk"></rect><circle cx="50" cy="50" r="40" stroke="#afafb7" fill="none" strokeWidth="10" strokeLinecap="round"></circle><circle cx="50" cy="50" r="40" stroke="#000000" fill="none" strokeWidth="6" strokeLinecap="round"><animate attributeName="stroke-dashoffset" dur="2s" repeatCount="indefinite" from="0" to="502"></animate><animate attributeName="stroke-dasharray" dur="2s" repeatCount="indefinite" values="150.6 100.4;1 250;150.6 100.4"></animate></circle></svg>
                :
                <div className="tb__icon tb-chevron-right"><div></div></div>
              }
            </div>

            <div className="splide__arrow--prev tb_cs_slider_pre_arrow-prev">
              <div className="tb__icon tb-chevron-left"><div></div></div>
            </div>


          </div>


        </Splide>
      </div>
    );
  }
}



const mapStateToProps = state => {
  const { appData } = state;
  return {
    appData: appData,
    loaderData: state.loaderData,
    newPostData: appData.newPostData,
    appendData: appData.postData.appendData,
    hasMoreData: appData.postData.hasMoreData[0] ? appData.postData.hasMoreData[0].hasMoreData == false ? false : appData.postData.hasMoreData[appData.postData.appendData.networkID] ? appData.postData.hasMoreData[appData.postData.appendData.networkID].hasMoreData : true : true
  }
}

const mapDispatchToProps = dispatch => {
  return {
    getDataNextSteps: (wallID, timeStamp, postCount, networkId, after, heightEvent, updateInStateNow) => dispatch(getDataNextSteps(wallID, timeStamp, postCount, networkId, after, heightEvent, updateInStateNow)),
    showPopUP: (data) => dispatch(showPopUP(data)),

  }
}
export default connect(mapStateToProps, mapDispatchToProps)(CircleSlider);